body {
    margin: 0;
    font-family: Open Sans, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: white;
    background-color: #09031c;
}

.all_container {
    width: 1450px;
    margin: 40px auto;
}

.mainPage {
    min-height: 100vh;
}

.link {
    font-size: larger;
    text-decoration: none;
    color: white;
    transition: 0.2s;
}

.link:hover {
    color: rgb(94, 42, 156);
}

.aboutMe {
    margin-top: 250px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.text {
    font-size: xxx-large;
    font-weight: 500;
}

.image {
    border-radius: 50%;
    width: 400px;
}

.blackout {
    min-height: 100vh;
}

.myProjects {
    margin: 0 auto;
    margin-top: 150px;
    text-align: center;
}

.btnGithub {
    padding: 8px;
    width: 160px;
    color: black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-decoration: none;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    transition: 0.3s;
}

.btnGithub:hover {
    background-color: rgb(94, 42, 156);
    color: white;
}

.btnSite {
    padding: 8px;
    width: 160px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    transition: 0.3s;
    background-color: rgb(94, 42, 156);
}

.btnSite:hover {
    background-color: rgb(58 9 116);
}

.btns {
    display: flex;
    justify-content: space-around;
}

.wrapperProjects {
    margin-top: 100px;
}

.mySkills {
    margin-top: 100px;
    min-height: 800px;
    text-align: center;
}

.wrapperSkills {
    margin-top: 150px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    height: 600px;
}

.skill {
    width: 210px;
}

.myContact {
    margin-top: 250px;
}

.contactsBlock {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.contacts {
    display: flex;
    align-items: center;
    width: 850px;
    justify-content: space-between;
}

.icons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 160px;
}

.footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 30px;
}